var transit = require('transit-js');

var Immutable = require('immutable');

function recordName(record) {
  /* eslint no-underscore-dangle: 0 */
  return record._name || record.constructor.name || 'Record';
}

function createReader(recordMap) {
  return transit.reader('json', {
    mapBuilder: {
      init: function init() {
        return {};
      },
      add: function add(m, k, v) {
        m[k] = v;
        return m;
      },
      finalize: function finalize(m) {
        return m;
      }
    },
    handlers: {
      iM: function iM(v) {
        var m = Immutable.Map().asMutable();

        for (var i = 0; i < v.length; i += 2) {
          m = m.set(v[i], v[i + 1]);
        }

        return m.asImmutable();
      },
      iOM: function iOM(v) {
        var m = Immutable.OrderedMap().asMutable();

        for (var i = 0; i < v.length; i += 2) {
          m = m.set(v[i], v[i + 1]);
        }

        return m.asImmutable();
      },
      iL: function iL(v) {
        return Immutable.List(v);
      },
      iS: function iS(v) {
        return Immutable.Set(v);
      },
      iOS: function iOS(v) {
        return Immutable.OrderedSet(v);
      },
      iR: function iR(v) {
        var RecordType = recordMap[v.n];

        if (!RecordType) {
          var msg = 'Tried to deserialize Record type named `' + v.n + '`, ' + 'but no type with that name was passed to withRecords()';
          throw new Error(msg);
        }

        return new RecordType(v.v);
      }
    }
  });
}

function createWriter(recordMap, predicate) {
  function mapSerializer(m) {
    var i = 0;

    if (predicate) {
      m = m.filter(predicate);
    }

    var a = new Array(2 * m.size);
    m.forEach(function (v, k) {
      a[i++] = k;
      a[i++] = v;
    });
    return a;
  }

  var handlers = transit.map([Immutable.Map, transit.makeWriteHandler({
    tag: function tag() {
      return 'iM';
    },
    rep: mapSerializer
  }), Immutable.OrderedMap, transit.makeWriteHandler({
    tag: function tag() {
      return 'iOM';
    },
    rep: mapSerializer
  }), Immutable.List, transit.makeWriteHandler({
    tag: function tag() {
      return "iL";
    },
    rep: function rep(v) {
      if (predicate) {
        v = v.filter(predicate);
      }

      return v.toArray();
    }
  }), Immutable.Set, transit.makeWriteHandler({
    tag: function tag() {
      return "iS";
    },
    rep: function rep(v) {
      if (predicate) {
        v = v.filter(predicate);
      }

      return v.toArray();
    }
  }), Immutable.OrderedSet, transit.makeWriteHandler({
    tag: function tag() {
      return "iOS";
    },
    rep: function rep(v) {
      if (predicate) {
        v = v.filter(predicate);
      }

      return v.toArray();
    }
  }), Function, transit.makeWriteHandler({
    tag: function tag() {
      return '_';
    },
    rep: function rep() {
      return null;
    }
  }), "default", transit.makeWriteHandler({
    tag: function tag() {
      return 'iM';
    },
    rep: function rep(m) {
      if (!('toMap' in m)) {
        var e = "Error serializing unrecognized object " + m.toString();
        throw new Error(e);
      }

      return mapSerializer(m.toMap());
    }
  })]);
  Object.keys(recordMap).forEach(function (name) {
    handlers.set(recordMap[name], makeRecordHandler(name, predicate));
  });
  return transit.writer('json', {
    handlers: handlers
  });
}

function makeRecordHandler(name) {
  return transit.makeWriteHandler({
    tag: function tag() {
      return 'iR';
    },
    rep: function rep(m) {
      return {
        n: name,
        v: m.toObject()
      };
    }
  });
}

function buildRecordMap(recordClasses) {
  var recordMap = {};
  recordClasses.forEach(function (RecordType) {
    var rec = new RecordType({});
    var recName = recordName(rec);

    if (!recName || recName === 'Record') {
      throw new Error('Cannot (de)serialize Record() without a name');
    }

    if (recordMap[recName]) {
      throw new Error('There\'s already a constructor for a Record named ' + recName);
    }

    recordMap[recName] = RecordType;
  });
  return recordMap;
}

function createInstance(options) {
  var records = options.records || {};
  var filter = options.filter || false;
  var reader = createReader(records);
  var writer = createWriter(records, filter);
  return {
    toJSON: function toJSON(data) {
      return writer.write(data);
    },
    fromJSON: function fromJSON(json) {
      return reader.read(json);
    },
    withFilter: function withFilter(predicate) {
      return createInstance({
        records: records,
        filter: predicate
      });
    },
    withRecords: function withRecords(recordClasses) {
      var recordMap = buildRecordMap(recordClasses);
      return createInstance({
        records: recordMap,
        filter: filter
      });
    }
  };
}

module.exports = createInstance({});